body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-frame-space{

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.main-content-layout{

    display: flex;
    flex-direction: column;


}

.logo {
    min-width: 120px;
    height: 31px;
    color: rgba(255,255,255,.7);
    margin: 16px 24px 16px 0;
    float: left;
    font-size: 20px;
    display:flex;
    justify-content: center;
    align-items: center;
}

.main-space{

    width:100%;
    min-height:800px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border: 1px #ddd solid;
    padding: 10px;
}

.title-Bar{

    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 25px;
    color: #282c34;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 20px;

}

.title-Bar-subtitle{

    margin-left: 20px;
    font-size: 20px;
    color: #282c34;
}

.home-tool-bar{

    height: 50px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
}



.g-text-button:hover{
    color: #41BBFD;
    border: 1px #41BBFD solid;
}

.g-text-button{
    color: #3A9EED;
    cursor:pointer;
    font-size: 14px;
    word-break: break-all;
    display: inline;
    border: 1px #ddd solid;
    padding: 5px;
    margin: 10px;

}
.detail-image-display-space{

    display: flex;

    justify-content: center;

    align-items: center;
}

.tag-detail-image-preview{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.sketch-space{

    border: 1px solid #ddd;
    background: #8cc8ff;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;
}

.draw-space{

    margin:10px;

}

.sketch-play-ground{

    display:flex;

}

.sketch-preview-space{

    display:flex;
    flex-direction: column;
    margin:5px;

}

.sketch-tool-bar{

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:flex-start;
    align-items: center;
}
